import React, { useState, useEffect } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';

function useLocalStorage(key, initialValue) {
  const [storedValue, setStoredValue] = useState(() => {
    const item = localStorage.getItem(key);
    return item ? JSON.parse(item) : initialValue;
  });

  useEffect(() => {
    localStorage.setItem(key, JSON.stringify(storedValue));
  }, [key, storedValue]);

  return [storedValue, setStoredValue];
}


function App() {
  const [weights, setWeights] = useLocalStorage('weights', []);
  const [currentWeight, setCurrentWeight] = useState('');

  const handleWeightChange = (event) => {
    setCurrentWeight(event.target.value);
  };

  const addWeight = () => {
    if (currentWeight !== '') {
      const newWeights = [...weights, { name: new Date().toLocaleString(), weight: parseFloat(currentWeight) }];
      setWeights(newWeights);
      setCurrentWeight('');
    }
  };

  return (
    <div className="App">
      <h1>Weight Tracker</h1>
      <div>
        <input type="number" value={currentWeight} onChange={handleWeightChange} />
        <button onClick={addWeight}>Add</button>
      </div>
      {weights.length > 0 && (
        <LineChart width={1000} height={600} data={weights}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Line type="monotone" dataKey="weight" stroke="#8884d8" />
        </LineChart>
      )}
    </div>
  );
}

export default App;